
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import SinceCompent from '../order/sinceCompent.vue';
import { API, getQuery } from '../../services';

export default {
directives: {
TransferDom
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
components: {
SinceCompent,
XHeader,
Group,
Cell,
CellBox,
Popup,
Toast
},
data() {
return {
orderId: "",
/* 是否选择地址 */
isSelect: "",
tplisSelect: "",
returnType: "",
confrim: "",
toast: false,
toasts: false,
/* 快递显示 */
tplShow: false,
/* 地址显示 */
sinceShow: false,
/* 快递公司列表 */
tplList: [
{
name: "顺丰快递",
id: "shunfeng",
},
{
name: "申通快递",
id: "shentong",
},
{
name: "中通快递",
id: "zhongtong",
},
{
name: "圆通快递",
id: "yuantong",
},
{
name: "韵达快递",
id: "yunda",
},
{
name: "EMS快递",
id: "emsinten",
},
{
name: "全峰快递",
id: "quanfeng",
},
{
name: "天天快递",
id: "tiantian",
},
{
name: "德邦快递",
id: "debang",
},
{
name: "百世快递",
id: "huitong",
},
{
name: "宅急送",
id: "zhaijisong",
},
{
name: "其他",
id: "auto",
},
],
/* 归还地址 */
sinceList: [],
infoData: {},
page: 1,
sincedata: {},
currentTpl: "",
code: "",
}
},

methods: {
close() {
this.tplShow = false;
this.sinceShow = false;
},
/* 获取数据 */
getData() {
API.order.orderInfo({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: this.orderId,
}).then((res) => {
if (res.body.code == 200) {
this.infoData = res.body.data;
if (this.returnType == 3) {
this.getSince();

} else {
this.getReturnList();
}
}
});
},
/* 获取自提点列表 */
getSince() {
API.order.goodsSince({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
province: this.infoData.order_province,
city: this.infoData.order_city,
region: this.infoData.order_district,
goodsId: this.infoData.goodsId,
page: this.page,
page_number: 10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
this.sinceList = this.sinceList.concat(res.body.data.ztd.data);
if (res.body.data.ztd.data.length == 10) {
this.page++;
this.getSince();
}
}
});
},
/* 获取归还地址列表 */
getReturnList() {
API.order.goodsRevert({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
goodsId: this.infoData.goodsId,
page: this.page,
page_number: 10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
this.sinceList = this.sinceList.concat(res.body.data.revert.data);
if (res.body.data.revert.data.length == 10) {
this.page++;
this.getReturnList();
}
}
});
},
/* 路由返回 */
routerBack() {
this.$router.goBack();
},
/* 打开弹窗 */
openPoup(type) {
if (type == 0) {
this.tplShow = true;
} else {
this.sinceShow = true;
}
},
/* 确认地址 */
confrimSince(item) {
this.sincedata = item;
this.isSelect = "已选择",
this.sinceShow = false;
},
/* 确认物流公司 */
confrimTpl(item) {
this.currentTpl = item.id;
this.tplisSelect = item.name,
this.tplShow = false;
},
/* 确认归还 */
orderBack() {
let self = this;
/* 快递 */
if (this.returnType == 1) {
if (!this.isSelect) {
this.confrim = "请输入归还地址";
this.toasts = true;
return false;
}
if (!this.tplisSelect) {
this.confrim = "请输入物流方式";
this.toasts = true;
return false;
}
if (!this.code) {
this.confrim = "请输入运单号码";
this.toasts = true;
return false;
}
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认归还',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.order.orderReturn({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: self.orderId,
sinceId: "",
expressId: self.code,
logisticsName: self.currentTpl,
revertId: self.sincedata.revert_id,
company: self.tplisSelect,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "归还成功";
localStorage.setItem("reload", "1");
setTimeout(() => {
self.$router.push({ path: '/index/main/order' });
}, 500);
}
});
}
});
} else {
if (!this.isSelect) {
this.confrim = "请输入归还地址";
this.toasts = true;
return false;
}
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认归还',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.order.orderReturn({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: self.orderId,
/* sinceId:self., */
expressId: "",
logisticsName: "",
revertId: "",
company: "",
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "归还成功";
localStorage.setItem("reload", "1");
setTimeout(() => {
self.$router.push({ path: '/index/main/order' });
}, 500);
}
});
}
});
}

}
},
activated() {
overscroll(document.querySelector('.return_main_tplbox'));
overscroll(document.querySelector('.return_main_sinceList'));
this.page = 1;
this.isSelect = "";
this.tplisSelect = "";
this.infoData = "";
this.sincedata = "";
this.code = "";
this.returnType = this.$route.query.type;
this.orderId = this.$route.query.orderId;
this.sinceList = [];
this.getData();
},
mounted() {
overscroll(document.querySelector('.return_main_tplbox'));
overscroll(document.querySelector('.return_main_sinceList'));
}
}



